import React from "react";
import FormFields from "./FormFields"; // Import the reusable FormFields component
import { Link } from "react-router-dom";

// Login Form Component
const LoginForm = () => (
  <form className="LoginFormWrp__form d-flex flex-column">
    <FormFields
      label="* Your Email"
      type="email"
      id="email"
      placeholder="Enter your email..."
    />
    <FormFields
      label="* Your Password"
      type="password"
      id="password"
      placeholder="Enter your password..."
    />

    <div className="d-flex flex-column gap-3 LoginFormWrp__btnWrp">
      <label>
        <input type="checkbox" /><div> I agree to the<Link to="/"> terms and conditions</Link></div>
      </label>

      <button type="submit" className="btn btn-white has-hover-blue">
        Login &#62;
      </button>
      <Link to="/forget-password">Forgot Password</Link>
    </div>
  </form>
);

export default LoginForm;
